import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthenticationService } from 'src/app/sign-in/services/authentication.service';
import { PartnerDetails, PartnerProfile, PartnerType } from 'src/app/sign-in/services/modals/user';
import { forbiddenNameValidator } from 'src/app/utilities/forbidden-name.directive';

@Component({
  selector: 'app-partner-signup',
  templateUrl: './partner-signup.component.html',
  styleUrls: ['./partner-signup.component.css']
})
export class PartnerSignupComponent implements OnInit {
  //partnerTypeGroup:FormGroup;
  showLoading:boolean;
  showError:boolean;
  errorMessage;
  partnerTypeList:any;
  objpartnerTypeList:any;
  isCodeRequested : boolean ;
  isCodeVerified:boolean;
  PPartnerDetails: PartnerProfile;

  isVerified: boolean;
  isEmailReadOnly:boolean;
  isBack:boolean;
  isNext:boolean;
  isSubmit:boolean;
  isCompanyView:boolean;
  isPartnerTypeView:boolean;
  authToken;
  //partnerTypeList :any;
  countryList: any;
  stateList: any;
  cityList: any;
  partnerForm: FormGroup;
  OTPCheck;
  reqObj :any ;
  partnerD: PartnerDetails;

  constructor(private authService:AuthenticationService, private fb:FormBuilder,private http:HttpClient,private router: Router) { 
    this.isCodeVerified = true;
   this.isCompanyView=false;
   this.isPartnerTypeView =true
    this.isNext = true;
    this.showLoading = true;
    this.partnerForm = this.fb.group({
      objPartnerDetails:  this.fb.group({
        OrganizationURL:[''],
        OrganizationName:[''],
        EmailID:new FormControl('',[
          Validators.required,
          Validators.pattern("^[a-z0-9._%+-]+@([a-z0-9.-])+\\.[a-z]{2,4}$"), 
          forbiddenNameValidator(/gasdl/i), 
          forbiddenNameValidator(/asdsd/i)]),
        FirstName:'',
        LastName:'',
        Password:'',
        MobileCountryCode:'',
        PhoneNumber:'',
        CountryName:'',
        StateName:'',
        CityName:'',
        EmailIdVerificationCode:'',
        EmailIDDisplay:'',
        ConfirmPassword:'',
        
      }),
      
      partnerTypeList:new FormArray([])
    })

    this.authService.partnerService('partnertype',0).subscribe(r => {
     // console.log(r['objMasterData']);
      this.partnerTypeList=r['objMasterData'];
      const formArray = this.partnerForm.get('partnerTypeList') as FormArray;
    this.partnerTypeList.forEach(x => formArray.push(new FormControl(false)));
    this.showLoading= false;
    })

  }

  get f() {
    return this.partnerForm.controls;
  }

  get fPartnerDetails() {
   return this.partnerForm.controls;
    ;
  }

  ngOnInit(): void {
    localStorage.removeItem('user');
    localStorage.removeItem('login');

    
    this.loadAuthentication().then(() => {
    //  console.log(this.authToken);
      this.http.get('https://www.universal-tutorial.com/api/countries/', {
        headers: {
          Authorization: 'Bearer ' + this.authToken.auth_token,
          Accept: 'application/json'
        }
      }).subscribe(r => {
        this.countryList = r;
       // console.log(r);
       });

    });
  }

  
  loadAuthentication() {
    return new Promise((resolve) => {
      this.http.get('https://www.universal-tutorial.com/api/getaccesstoken', {
        headers: {
          'api-token': 'CTQCMPxRc7bP-L8W7yShszLaxSfP4t7Z2E6wngiVA4ureKDli-4yzuSUq6mwGvBIWYA',
          'user-email': 'raj@signulu.com',
          Accept: 'application/json'
        }
      }).subscribe(response => {
        this.authToken = response;
        ///console.log(this.authToken);
        resolve();
      });
    });
  }

  conuntryChange() {
    this.http.get('https://www.universal-tutorial.com/api/states/' + this.partnerForm.controls['objPartnerDetails'].get('CountryName').value, {
      headers: {
        Authorization: 'Bearer ' + this.authToken.auth_token,
        Accept: 'application/json'
      }
    }).subscribe(r => {
      this.stateList = r;
    });
  }

  stateChange() {
    this.http.get('https://www.universal-tutorial.com/api/cities/' + this.partnerForm.controls['objPartnerDetails'].get('StateName').value, {
      headers: {
        Authorization: 'Bearer ' + this.authToken.auth_token,
        Accept: 'application/json'
      }
    }).subscribe(r => {
      this.cityList = r;
     // console.log(r);
    });
  }

  RequestCode(){
    this.showError=false;
 //   console.log("##############################-----------"+this.partnerForm.controls['objPartnerDetails'].get('EmailID').status)
 //    

    if(this.partnerForm.controls['objPartnerDetails'].get('EmailID').status == 'VALID' && this.partnerForm.controls['objPartnerDetails'].get('EmailID').value && this.partnerForm.controls['objPartnerDetails'].get('OrganizationName').value 
    )
    {   
      this.isEmailReadOnly = true;
        this.authService.sendEmailVerificationCode(this.partnerForm.controls['objPartnerDetails'].get('EmailID').value).subscribe(r => {
          //  console.log(r);
            if (r['IsSuccessful']) {
              this.OTPCheck = r['OTPCode'];
              this.isCodeRequested = true;
            }else
            {
              this.isEmailReadOnly = false;
            //  alert(r['ErrorMessage']);
              this.showError=true;
              this.errorMessage=r['ErrorMessage'];
            }

          })
    }else
    {

      this.isEmailReadOnly = false;

      if(!this.partnerForm.controls['objPartnerDetails'].get('OrganizationName').value)
      {
        this.showError=true;
        this.errorMessage="Please Enter Company Name";
      }
      // else if(!this.partnerForm.controls['objPartnerDetails'].get('OrganizationURL').value)
      // {
      //   this.showError=true;
      //   this.errorMessage="Please Enter Company Website";
      // }
      else if(!this.partnerForm.controls['objPartnerDetails'].get('EmailID').value)
      {
        this.showError=true;
        this.errorMessage="Please Enter Company Email";
      }
      else if(this.partnerForm.controls['objPartnerDetails'].get('EmailID').status != 'VALID')
      {
        this.showError=true;
        this.errorMessage="Please Enter Valid Company Email";
      }else
      {
        this.showError=false;
      }
    }

  }

  Verify(){ 
    this.showError=false;
      if(this.partnerForm.controls['objPartnerDetails'].get('EmailIdVerificationCode').value == this.OTPCheck)
      {
        this.isVerified = true;
        this.isCodeVerified =  false;
        this.isCompanyView = false;
        this.isSubmit= true;
        this.partnerForm.controls['objPartnerDetails'].get('EmailIDDisplay').setValue(this.partnerForm.controls['objPartnerDetails'].get('EmailID').value);
      }else if(!this.partnerForm.controls['objPartnerDetails'].get('EmailIdVerificationCode').value)
      {
    
        this.showError=true;
        this.errorMessage="Please Enter Email Verificaiton Code";
      }else{
        this.showError=true;
        this.errorMessage="Entered Wrong Email Varificaiton Code";
       
      }
    
  }
  typeChecked()
  {
   //  
  }
  
  onSubmit()
  {
    this.showError=false;
    if(this.partnerForm.controls['objPartnerDetails'].get('FirstName').value 
          && this.partnerForm.controls['objPartnerDetails'].get('LastName').value 
          && this.partnerForm.controls['objPartnerDetails'].get('Password').value 
          && this.partnerForm.controls['objPartnerDetails'].get('PhoneNumber').value
          && this.partnerForm.controls['objPartnerDetails'].get('MobileCountryCode').value)
      {

          const ary = this.partnerForm.get('partnerTypeList').value;
          this.objpartnerTypeList =[];
          for (let i = 0; i < ary.length; i++) {
           // console.log(i +":"+ary[i]);
            if(ary[i])
            {
              
              this.objpartnerTypeList.push({
                "PartnerTypeID":this.partnerTypeList[i]['MasterID']
              });
            }
          }
        
       const JSONOBJ= {
        "objPartnerDetails": this.partnerForm.controls['objPartnerDetails'].value,
        "partnerTypeList": this.objpartnerTypeList
       }
     
        this.authService.RegisterNewPartner(JSON.parse(JSON.stringify(JSONOBJ))).subscribe(r => {
         // console.log(r);
          if (r['IsSuccessful']) {
            this.router.navigate(['thanks']);
          } else {
         //   alert(r['ErrorMessage']);
            this.showError=true;
            this.errorMessage=r['ErrorMessage'];
           
          }

        }, err => console.log(err));


      }else{
        if(!this.partnerForm.get('FirstName').value)
        {
          this.showError=true;
            this.errorMessage='Enter FirstName';
           
        }
        else if(!this.partnerForm.get('LastName').value)
        {
          this.showError=true;
            this.errorMessage='Enter LastName';
           
        }
      }
           
  }


   onBackCLicked() {
    this.showError=false;
         if(this.isCompanyView)
        {
          this.isPartnerTypeView=true;
          this.isCompanyView = false;
          this.isBack=false;
          this.isNext = true;
          this.isSubmit=false;

         
        }else if(this.isVerified)
        {
          this.isPartnerTypeView=false;
          this.isCompanyView = true;
          this.isVerified= false;
          this.isBack=true;
          this.isNext = true;
          this.isSubmit=false;
          this.isCodeVerified =false;

        }
   }

   checkPartnerChecked()
   {
    this.showError=false;
    const ary = this.partnerForm.get('partnerTypeList').value;
    this.objpartnerTypeList =[];
    for (let i = 0; i < ary.length; i++) {
     // console.log(i +":"+ary[i]);
      if(ary[i])
      {
        
       return true;
      }
    }
    return false;
   }
   onNextCLicked() {
    this.showError=false;
     if(this.isPartnerTypeView)
     {
       if( this.checkPartnerChecked())
       {

          this.isPartnerTypeView=false;
          this.isCompanyView = true;
          this.isBack=true;
          this.isNext=false;

          if(!this.isCodeVerified)
          {
            this.isNext=true;
          }
        
       }else{
        this.showError=true;
        this.errorMessage="Select atleast one partner with. ";
      
       }
     }else if(this.isCompanyView)
     {
        this.isPartnerTypeView=false;
        this.isCompanyView = false;
        this.isCodeVerified =true;
        this.isVerified = true;
        this.isNext=false;
        this.isBack=true;
        this.isSubmit=true;
     }
       
    

  }

  get getEmailID(){
    return this.partnerForm.controls['objPartnerDetails'].get('EmailID')
    }
}
