import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../sign-in/services/authentication.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-landing-page-home',
  templateUrl: './landing-page-home.component.html',
  styleUrls: ['./landing-page-home.component.css']
})
export class LandingPageHomeComponent implements OnInit {
  

  isSignIn;
  constructor(public authService: AuthenticationService,private router: Router, ) {
    this.isSignIn = this.authService.getIsSigned();

    if (this.isSignIn) {
      this.router.navigate(['partner']);
    } else {
      this.router.navigate(['']);

    }
   }

  ngOnInit(): void {
   // const body = document.getElementsByTagName('body')[0];
  //   body.classList.add('container');


  
  }

  
}
