import { LandingPageLayoutComponent } from './landing-page-layout/landing-page-layout.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ParternWithSignuluComponent } from './partern-with-signulu/partern-with-signulu.component';
import { LandingPageHomeComponent } from './landing-page-home/landing-page-home.component';
import { AuthGuard } from '../utilities/auth.guard';
import { LearningCentralComponent } from './learning-central/learning-central.component';
import { MembershipOffersComponent } from './membership-offers/membership-offers.component';
import { CompetenciesComponent } from './competencies/competencies.component';

import { BenefitsComponent } from './benefits/benefits.component';
import { SupportComponent } from './support/support.component';
import { PartnerSignupComponent } from './partner-signup/partner-signup.component';
import { PartnerThankqComponent } from './partner-thankq/partner-thankq.component';
import { MakePaymentComponent } from './make-payment/make-payment.component';


const routes: Routes = [
  {
    path: '',
    component: LandingPageLayoutComponent,
    children: [{
      path: '',
      component: LandingPageHomeComponent
    },
    {
      path: 'partner',
      component: ParternWithSignuluComponent,
      canActivate: [AuthGuard] 
    },
    {
      path: 'learning-central',
      component: LearningCentralComponent,
      
    },
    {
      path: 'membership-offers',
      component: MembershipOffersComponent,
      
    },
    {
      path: 'competencies',
      component: CompetenciesComponent,
      
    },
    {
      path: 'benefits',
      component: BenefitsComponent,
      
    },
    {
      path: 'support',
      component: SupportComponent,
      
    },
    {
      path: 'signup',
      component: PartnerSignupComponent,
      
    },
    {
      path: 'thanks',
      component: PartnerThankqComponent,
      
    },
    {
      path: 'powerpack-payment',
      component: MakePaymentComponent,
      
    },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LandingPageRoutingModule { }
