import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { AuthenticationService } from 'src/app/sign-in/services/authentication.service';

@Component({
  selector: 'app-make-payment',
  templateUrl: './make-payment.component.html',
  styleUrls: ['./make-payment.component.css']
})
export class MakePaymentComponent implements OnInit {
  emailNotifID:Number;
  amount:Number;
  constructor(private route:Router,private authService:AuthenticationService) {
    this.route.routerState.root.queryParams.subscribe(params => {
    
       
      this.authService.GetAmount(Number(params.id)).subscribe(r => {
         
          if (r['IsSuccessful']) {
           this.amount = r['NewID'];
            window.location.href = "upi://pay?pa=mitisphere@icici&pn=MITI SPHERE SOLUTIONS P LTD&tr=EZYS&tr=EZYS9710101089&cu=INR&mc=5411&am="+this.amount;

          }
       })
    });

   }

  ngOnInit(): void {
 
   }

}
